import * as _ from "lodash";
import { pixelRatio } from "@/config";

let pixels = [];
let THRESHOLD = 128;

export const PixelNoise = (ref, srcRef, event, { top, value, diff }) => {
  // console.log(
  //   "skewdRight value",
  //   "スクロール総量",
  //   top,
  //   "スクロール量",
  //   value,
  //   diff
  // );

  const ctxSrc = srcRef.getContext("2d");
  const rect = srcRef.getBoundingClientRect();
  rect.width = rect.width * pixelRatio;
  rect.height = rect.height * pixelRatio;
  const src = ctxSrc.getImageData(0, 0, rect.width, rect.height);
  let dst = ctxSrc.createImageData(rect.width, rect.height);

  for (let y = 0; y < rect.height; y++) {
    for (let x = 0; x < rect.width; x++) {
      const i = Math.floor(y * (rect.width * 4) + x * 4);
      let z = (src.data[i] + src.data[i + 1] + src.data[i + 2]) / 3;

      //コピー
      dst.data[i] = src.data[i];
      dst.data[i + 1] = src.data[i + 1];
      dst.data[i + 2] = src.data[i + 2];
      dst.data[i + 3] = src.data[i + 3];

      if (z < THRESHOLD) {
        const rr = Math.floor(Math.random() * 360); //回転初期値
        const r = Math.random() * 1 + 1; //半径初期値
        const s = Math.random() * 2; //回転速度初期値
        const a = src.data[i + 3];
        //ノイズのピクセルカラー
        const rgb = {
          r: 0,
          g: 0,
          b: 0
        };
        pixels.push({ index: i, x, y, rr, r, s, a, rgb });
      }
    }
  }

  //1pxスクロールあたりの進行ピクセル数
  const pixRate = +(rect.width / (event.to - event.from)).toFixed(2);

  let col = [];

  pixels.map(pix => {
    const rotate = value * pix.s;
    const radius = value / 5 + pix.r; //半径
    const radian = ((pix.rr + rotate) * Math.PI) / 180;
    const x = Math.floor(radius * Math.cos(radian)) + pix.x;
    const y = Math.floor(radius * Math.sin(radian)) + pix.y;
    //描画
    const i = Math.floor(y * (rect.width * 4) + x * 4);
    dst.data[i] = pix.rgb.r;
    dst.data[i + 1] = pix.rgb.g;
    dst.data[i + 2] = pix.rgb.b;
    dst.data[i + 3] = pix.a;
  });

  ctxSrc.putImageData(dst, rect.x, rect.y);
  const ctx = ref.getContext("2d");
  ctx.drawImage(
    srcRef,
    0,
    0,
    rect.width,
    rect.height,
    0,
    0,
    rect.width,
    rect.height
  );
};
